.wp-block-create-block-about-alumni-block {
	background-color: #21759b;
	color: black;
	padding: 2px;
}

.alumni {
	background-color: #ffebfb;
	width: 100%;
	display: flex;
	height: 500px;
	flex-direction: row;
	justify-content: center;
	align-items: flex-start;
}

.alumni__container {
	width: 80%;
	max-width: 1400px;
	display: flex;
	flex-direction: column;
	h3 {
		align-self: flex-start;
		margin-bottom: 20px;
	}
}
.alumni__content__container {
	position: relative;
	img {
		position: absolute;
		left: 0;
		border-radius: 50%;
		height: 360px;
		width: 360px;
		object-fit: cover;
		z-index: 1;
	}
}
.alumni__content {
	background-color: #c4d7bc;
	width: calc(100% - 180px);
	height: 360px;
	position: absolute;
	right: 0;
	border-radius: 10px;
}
.alumni__title,
.alumni__subtitle,
.alumni__body {
	margin-left: 250px;
	margin-right: 100px;
}
.alumni__title {
	margin-top: 20px;
}
.alumni__subtitle {
	margin-bottom: 10px;
}
@media screen and (max-width: 1100px) {
	.alumni {
		height: auto;
	}
	.alumni__title,
	.alumni__subtitle,
	.alumni__body {
		margin-left: 10px;
		margin-right: 10px;
	}
	.alumni__container {
		justify-content: center;
		justify-self: center;
		width: auto;
	}
	.alumni__content__container {
		height: 800px;
		width: 360px;
		background-color: #ffebfb;
		position: relative;
		margin-bottom: 30px;
		img {
			border-radius: 10% 10% 0 0;
			height: 360px;
			width: 100%;
			position: absolute;
			top: 0;
			object-fit: cover;
			z-index: 1;
		}
		.alumni__content {
			background-color: #c4d7bc;
			box-shadow: 8px 8px 0px #e0efda;
			position: absolute;
			bottom: 0;
			border-radius: 10px;
			min-height: 512px;
			width: 100%;

			.alumni__card__content {
				margin: 80px 20px 24px 20px;
			}
		}
	}
}
